import React from "react";
import { connect } from "react-redux";
import Basket from "./Basket";
import Preloader from "../common/Preloader";
import { updateBasketCount } from "../../redux/headerReducer";
// updateBasketCount={this.props.updateBasketCount}
import * as axios from "axios";
import { setBasketProducts, deleteProductFromLocalStorage, toggleIsFething, onchangeStateTextarea } from "../../redux/basketReducer";

class BasketAPIComponent extends React.Component {
  basketProductUrlCreator = () => {
    var url = "/api/products/basketproducts?";
    JSON.parse(localStorage.getItem("mybasket")).forEach((element) => {
      url = url + "array=" + element + "&";
    });
    return url;
  };

  componentDidMount() {
    axios.get(this.basketProductUrlCreator()).then((response) => {
      this.props.setBasketProducts(response.data);
      this.props.toggleIsFething(false);
    });
  }

  deleteProductFromBasket = (product) => {
    this.props.deleteProductFromLocalStorage(product);
    axios.get(this.basketProductUrlCreator()).then((response) => {
      this.props.setBasketProducts(response.data);
      this.props.toggleIsFething(false);
      this.props.updateBasketCount();
    });
  };

  toOrder = () => {
    let that = this;
    axios
      .post("/api/order", {
        phoneNum: that.props.phoneNum,
        orderProducts: that.props.basketProducts.map((u) => {
          return { productId: u.id };
        }),
      })
      .then(function (response) {
        alert("–ó–∞–∫–∞–∑ –æ—Ñ–æ—Ä–º–ª–µ–Ω, –∂–¥–∏—Ç–µ –∑–≤–æ–Ω–∫–∞");
        localStorage.setItem("mybasket", JSON.stringify([]));
        that.props.setBasketProducts([]);
        that.props.updateBasketCount();
      });
  };

  render() {
    return <>{this.props.isFething === false ? <Basket deleteProductFromBasket={this.deleteProductFromBasket} toOrder={this.toOrder} phoneNum={this.props.phoneNum} basketProducts={this.props.basketProducts} onchangeStateTextarea={this.props.onchangeStateTextarea} /> : <Preloader />}</>;
  }
}

let mapStateToProps = (state) => {
  return {
    basketProducts: state.basket.basketProducts,
    isFething: state.basket.isFething,
    phoneNum: state.basket.phoneNum,
  };
};

const BasketConteiner = connect(mapStateToProps, { deleteProductFromLocalStorage, onchangeStateTextarea, setBasketProducts, updateBasketCount, toggleIsFething })(BasketAPIComponent);
export default BasketConteiner;
