import React from "react";
import { NavLink } from "react-router-dom";
import "bootstrap/dist/js/bootstrap.bundle.min";

let Basket = (props) => {
  return (
    <article className="flex-grow-1 mt-5 d-flex flex-column">
      <p className=" text-uppercase text-center display-4 font-weight-bold">–ö–æ—Ä–∑–∏–Ω–∞</p>
      <div className=" container-fluid">
        <div className="row  justify-content-center  row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4 row-cols-xl-5">
          {props.basketProducts.map((data, index) => (
            <div key={"1"+index} className="col nav-link d-flex flex-column  text-dark align-items-start p-3 m-5">
              <img key={"2"+index} className=" w-75 align-self-center" alt="" src={data.imagesUrl}></img>
              <p key={"3"+index} className="h2 font-weight-light">{data.name}</p>
              <p key={"4"+index} className="h5 font-weight-bold ">{data.price} ‚ÇΩ</p>
              <NavLink key={"5"+index} to={"/product/" + data.id} type="button" className="btn m-2 btn-secondary rounded-0 text-uppercase ">
                –ü–æ–¥—Ä–æ–±–Ω–µ–µ
              </NavLink>
              <button key={"6"+index} className="btn btn-danger m-2 rounded-0 text-uppercase " id={data.id}  onClick={(e)=>props.deleteProductFromBasket(e.target.id)}>
                —É–¥–∞–ª–∏—Ç—å
              </button>
            </div>
          ))}
        </div>
      </div>
      {JSON.parse(localStorage.getItem('mybasket')).length === 0 ? (
        <p className="text-uppercase text-center display-4 text-danger">–ø—É—Å—Ç–æ</p>
      ) : (
        <button type="button" className="btn btn-success align-self-center mb-5" data-toggle="modal" data-target="#exampleModal">
          <p className="m-0 text-uppercase display-4">–∑–∞–∫–∞–∑–∞—Ç—å</p>
        </button>
      )}

      {/* ------------------ */}
      <div className="modal fade" id="exampleModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                –í–≤–µ–¥–∏—Ç–µ —Å–≤–æ–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ —á—Ç–æ–± –º—ã –º–æ–≥–ª–∏ —Å–≤—è–∑–∞—Ç—å—Å—è —Å –≤–∞–º–∏ –¥–ª—è –¥–∞–ª—å–Ω–µ–π—à–µ–≥–æ –æ—Ñ—Ä–º–ª–µ–Ω–∏—è –∑–∞–∫–∞–∑–∞
              </h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <input type="text" className="form-control"  value={props.phoneNum || ''} onChange={(e)=>props.onchangeStateTextarea(e.target.value|| "", "num")} id="exampleInputEmail1" aria-describedby="emailHelp" />
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-dismiss="modal">
                –ó–∞–∫—Ä—ã—Ç—å
              </button>

              <button type="button" onClick={() => props.toOrder()} data-dismiss="modal" className="btn btn-success">
                –û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑
              </button>
            </div>
          </div>
        </div>
      </div>
      {/* ------------------ */}
    </article>
  );
};
export default Basket;
